<template>
    <div class="header">
        <!-- 折叠按钮 -->
        <div class="collapse-btn" @click="collapseChage">
            <i v-if="!collapse" class="el-icon-s-fold"></i>
            <i v-else class="el-icon-s-unfold"></i>
        </div>
        <div class="logo">机械制造技术基础课程设计系统</div>
        <div class="header-right">
            <div class="header-user-con">
                <!-- 全屏显示 -->
                <div class="btn-fullscreen" @click="handleFullScreen">
                    <el-tooltip effect="dark" :content="fullscreen?`取消全屏`:`全屏`" placement="bottom">
                        <i class="el-icon-rank"></i>
                    </el-tooltip>
                </div>
                <!-- 消息中心 -->
                <!-- <div class="btn-bell">
                    <el-tooltip
                        effect="dark"
                        :content="message?`有${message}条未读消息`:`消息中心`"
                        placement="bottom"
                    >
                        <router-link to="/tabs">
                            <i class="el-icon-bell"></i>
                        </router-link>
                    </el-tooltip>
                    <span class="btn-bell-badge" v-if="message"></span>
                </div> -->
                <!-- 用户头像 -->
                <div class="user-avator">
                    <img v-if="types==0" src="../../assets/img/student.jpg"  />
                    <img v-if="types==1" src="../../assets/img/teacher.jpg" />
                    <img v-if="types==2" src="../../assets/img/teacher.jpg"  />
                </div>
                <!-- 用户名下拉菜单 -->
                <el-dropdown class="user-name" trigger="click" @command="handleCommand">
                    <span class="el-dropdown-link">
                        {{username}}
                        <i class="el-icon-caret-bottom"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <!-- <a href="https://github.com/lin-xin/vue-manage-system" target="_blank">
                            <el-dropdown-item>项目仓库</el-dropdown-item>
                        </a> -->
                        <el-dropdown-item v-if="types!=0" command="opens">修改密码</el-dropdown-item>
                        <el-dropdown-item divided command="loginout">退出登录</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </div>
        </div>
        <el-dialog title="修改密码" class="datas-bg" :visible.sync="editVisible" width="380px">
            <el-input class="margin10" v-model="password" size="small" placeholder="请输入旧密码"></el-input>
            <el-input class="margin10" v-model="newpassword" size="small" placeholder="请输入新密码"></el-input>
            <el-button type="primary" @click="modifyPw" style="display:block;margin: auto;" size="small">确 定</el-button>
        </el-dialog>
    </div>
</template>
<script>
import bus from '../common/bus';
import cookie from '../../utils/cookie'
import {fetchaUpdatepassword, fetchtUpdatepassword} from '../../axios/requests.js'
export default {
    data() {
        return {
            editVisible:false,
            password:'',
            newpassword:'',

            types:'',
            collapse: false,
            fullscreen: false,
            name: '未知',
            message: 2
        };
    },
    computed: {
        username() {
            let username = cookie.getCookie('name');
            return username ? unescape(username) : this.name;
        }
    },
    methods: {
        modifyPw(){
            if (this.types==1) {
                this.tUpdatepassword()
            }else if(this.types==2){
                this.aUpdatepassword()
            }
        },
        async tUpdatepassword() {//修改laoshi密码
          let {password, newpassword} = this;
            if (!password) {this.$message.error('旧密码不能为空');return};
            if (!newpassword) {this.$message.error('新密码不能为空');return};
             const loading = this.$loading({
              lock: true,
              text: '加载中...',
              spinner: 'el-icon-loading'
            });
            try {
              let response = await fetchtUpdatepassword(password, newpassword);
              if (response.status.code ==0) {
                this.$message({
                  message: '密码修改成功',
                  type: 'success'
                });
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              loading.close()
            }
        },
        async aUpdatepassword() {//修改管理员密码
          let {password, newpassword} = this;
            if (!password) {this.$message.error('旧密码不能为空');return};
            if (!newpassword) {this.$message.error('新密码不能为空');return};
             const loading = this.$loading({
              lock: true,
              text: '加载中...',
              spinner: 'el-icon-loading'
            });
            try {
              let response = await fetchaUpdatepassword(password, newpassword);
              if (response.status.code ==0) {
                this.$message({
                  message: '密码修改成功',
                  type: 'success'
                });
                cookie.delCookie('adminToken');
                cookie.delCookie('id');
                cookie.delCookie('name');
                this.$router.push('adminlogin');
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              loading.close()
            }
        },

        // 用户名下拉菜单选择事件
        handleCommand(command) {
            if (command == 'loginout') {
                let type = localStorage.getItem('type');
                var tokenames = ['studentToken', 'teacherToken', 'adminToken']
                cookie.delCookie(tokenames[type]);
                cookie.delCookie('id');
                cookie.delCookie('name');
                var urls = ['/login', '/teacherlogin', '/adminlogin'];
                this.$router.push(urls[type]);
            }else if(command=='opens'){
                this.editVisible=true
            }
        },
        // 侧边栏折叠
        collapseChage() {
            this.collapse = !this.collapse;
            bus.$emit('collapse', this.collapse);
        },
        // 全屏事件
        handleFullScreen() {
            let element = document.documentElement;
            if (this.fullscreen) {
                if (document.exitFullscreen) {
                    document.exitFullscreen();
                } else if (document.webkitCancelFullScreen) {
                    document.webkitCancelFullScreen();
                } else if (document.mozCancelFullScreen) {
                    document.mozCancelFullScreen();
                } else if (document.msExitFullscreen) {
                    document.msExitFullscreen();
                }
            } else {
                if (element.requestFullscreen) {
                    element.requestFullscreen();
                } else if (element.webkitRequestFullScreen) {
                    element.webkitRequestFullScreen();
                } else if (element.mozRequestFullScreen) {
                    element.mozRequestFullScreen();
                } else if (element.msRequestFullscreen) {
                    // IE11
                    element.msRequestFullscreen();
                }
            }
            this.fullscreen = !this.fullscreen;
        }
    },
    mounted() {
        if (document.body.clientWidth < 1300) {
            this.collapseChage();
        }
        this.types = localStorage.getItem('type');
    }
};
</script>
<style scoped>
.header {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 70px;
    font-size: 22px;
    color: #fff;
}
.collapse-btn {
    float: left;
    padding: 0 21px;
    cursor: pointer;
    line-height: 70px;
}
.header .logo {
    float: left;
    width: 320px;
    line-height: 70px;
}
.header-right {
    float: right;
    padding-right: 50px;
}
.header-user-con {
    display: flex;
    height: 70px;
    align-items: center;
}
.btn-fullscreen {
    transform: rotate(45deg);
    margin-right: 5px;
    font-size: 24px;
}
.btn-bell,
.btn-fullscreen {
    position: relative;
    width: 30px;
    height: 30px;
    text-align: center;
    border-radius: 15px;
    cursor: pointer;
}
.btn-bell-badge {
    position: absolute;
    right: 0;
    top: -2px;
    width: 8px;
    height: 8px;
    border-radius: 4px;
    background: #f56c6c;
    color: #fff;
}
.btn-bell .el-icon-bell {
    color: #fff;
}
.user-name {
    margin-left: 10px;
}
.user-avator {
    margin-left: 20px;
}
.user-avator img {
    display: block;
    width: 40px;
    height: 40px;
    border-radius: 50%;
}
.el-dropdown-link {
    color: #fff;
    cursor: pointer;
}
.el-dropdown-menu__item {
    text-align: center;
}
.margin10{
    margin-bottom: 10px;
}
</style>



