<template>
    <div class="sidebar">
        <el-menu
            class="sidebar-el-menu"
            :default-active="onRoutes"
            :collapse="collapse"
            background-color="#324157"
            text-color="#bfcbd9"
            active-text-color="#20a0ff"
            unique-opened
            router
        >
            <template v-for="item in items">
                <template v-if="item.subs">
                    <el-submenu :index="item.index" :key="item.index">
                        <template slot="title">
                            <i :class="item.icon"></i>
                            <span slot="title">{{ item.title }}</span>
                        </template>
                        <template v-for="subItem in item.subs">
                            <el-submenu
                                v-if="subItem.subs"
                                :index="subItem.index"
                                :key="subItem.index"
                            >
                                <template slot="title">{{ subItem.title }}</template>
                                <el-menu-item
                                    v-for="(threeItem,i) in subItem.subs"
                                    :key="i"
                                    :index="threeItem.index"
                                >{{ threeItem.title }}</el-menu-item>
                            </el-submenu>
                            <el-menu-item
                                v-else
                                :index="subItem.index"
                                :key="subItem.index"
                            >{{ subItem.title }}</el-menu-item>
                        </template>
                    </el-submenu>
                </template>
                <template v-else>
                    <el-menu-item :index="item.index" :key="item.index" @click="getnum">
                        <i :class="item.icon"></i>
                        <span slot="title">{{ item.title }} <span v-if="item.title=='消息中心'&&num>0" class="dian">{{num}}</span></span>
                    </el-menu-item>
                </template>
            </template>
        </el-menu>
    </div>
</template>

<script>
import bus from '../common/bus';
import {fetchCountstudentunreadmessage, fetchCountteacherunreadmessage} from '../../axios/requests'
export default {
    data() {
        return {
            num:0,
            collapse: false,
            items:''
        };
    },
    mounted(){
        let type = localStorage.getItem('type');
        if (type==1) {
            this.items = [
                // {
                //     icon: 'el-icon-s-home',
                //     index: 'dashboard',
                //     title: '系统首页'
                // },
                {
                    icon: 'el-icon-c-scale-to-original',
                    index: 'course',
                    title: '课程设计'
                },
                {
                    icon: 'el-icon-notebook-2',
                    index: 'works',
                    title: '作业项目'
                },
                {
                    icon: 'el-icon-folder-opened',
                    index: 'datas',
                    title: '基础资料'
                },
                {
                    icon: 'el-icon-folder-opened',
                    index: 'talk',
                    title: '教师讨论区'
                },
                {
                    icon: 'el-icon-message',
                    index: 'message',
                    title: '消息中心'
                },
            ]
        }else if(type==0){
            this.items = [
                {
                    icon: 'el-icon-c-scale-to-original',
                    index: 'stucourse',
                    title: '课程设计'
                },
                {
                    icon: 'el-icon-message',
                    index: 'stumessage',
                    title: '消息中心'
                }
            ]
        }else if(type==2){
            this.items = [
                {
                    icon: 'el-icon-s-check',
                    index: 'stumanage',
                    title: '学生管理'
                },
                {
                    icon: 'el-icon-s-custom',
                    index: 'teachermanage',
                    title: '教师管理'
                },
                {
                    icon: 'el-icon-s-opportunity',
                    index: 'admin',
                    title: '管理员管理'
                },
                {
                    icon: 'el-icon-s-operation',
                    index: 'schoolmanage',
                    title: '学校组织管理'
                }
            ]
        }
        
        if (type==0) {
            this.Countstudentunreadmessage()
        }else if(type==1){
            this.Countteacherunreadmessage()
        }
    },
    computed: {
        onRoutes() {
            return this.$route.path.replace('/', '');
        }
    },
    methods:{
        getnum(){
            let type = localStorage.getItem('type');
            if (type==0) {
                this.Countstudentunreadmessage()
            }else if(type==1){
                this.Countteacherunreadmessage()
            }
        },
        async Countstudentunreadmessage() {
            this.loading = true;
            let {id} = this;
            try {
              let response = await fetchCountstudentunreadmessage();
              if (response.status.code ==0) {
                this.num = response.result.total;
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              this.loading = false;
            }
        },
        async Countteacherunreadmessage() {
            this.loading = true;
            let {id} = this;
            try {
              let response = await fetchCountteacherunreadmessage();
              if (response.status.code ==0) {
                this.num = response.result.total;
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              this.loading = false;
            }
        },
    },
    created() {
        // 通过 Event Bus 进行组件间通信，来折叠侧边栏
        bus.$on('collapse', msg => {
            this.collapse = msg;
            bus.$emit('collapse-content', msg);
        });
        let type = localStorage.getItem('type');
        
    }
};
</script>

<style scoped>
.sidebar {
    display: block;
    position: absolute;
    left: 0;
    top: 70px;
    bottom: 0;
    overflow-y: scroll;
}
.sidebar::-webkit-scrollbar {
    width: 0;
}
.sidebar-el-menu:not(.el-menu--collapse) {
    width: 200px;
}
.sidebar > ul {
    height: 100%;
}
.dian{
    display:inline-block;
    width:25px;
    height:14px;
    border-radius: 14px;
    background:#f56c6c;
    margin-top: -10px;
    color:#fff;
    text-align: center;
    font-size: 10px;
    line-height: 14px;
}
</style>



